<style>
#styled-input {
  height: 20px;
  font-size: 30pt;
  width: 10px;
}
.styled-input label[for] {
  height: 30px;
  font-size: 20pt;
}
.btn {
  margin: 5px;
  text-align: center;
}
</style>
<template>
  <div>
    <loading :active.sync="isDataLoading" :can-cancel="true" :is-full-page="false"></loading>
    <v-card class="mt-5 elevation5">
      <v-row class="mx-2">
        <v-spacer></v-spacer>

        <!-- <v-btn
          large
          depressed=""
          color="primary"
          class=" m-2 subtitle-1"
          :to="{ name: 'inventory_tab' }"
        >
          <v-icon left>mdi-plus</v-icon>
          नयाँ दर्ता
        </v-btn> -->
        <v-btn
          large
          depressed=""
          color="primary"
          class=" m-2 subtitle-1"
          :to="{ name: 'buildinginformation' }"
        >
          <v-icon left>mdi-plus</v-icon>
          नयाँ दर्ता
        </v-btn>
        <v-btn
          large
          depressed=""
          color="primary"
          class=" m-2 subtitle-1"
          :to="{ name: 'dashboard' }"
        >
          Back
        </v-btn>
      </v-row>

      <div>
        <!-- <loading :active.sync="isDataLoading" :can-cancel="true" :is-full-page="false"></loading> -->
        <v-toolbar color="primary" dark flat>
          <v-toolbar-title>
            Inventory List
          </v-toolbar-title>
        </v-toolbar>
        <!-- <v-data-table
          :headers="headers"
          :items="items"
          :search="search"
          :hide-default-header="false"
          :items-per-page="10"
          :loading="isLoading"
          hide-default-footer
          item-key="name"
          class="sober elevation-12"
        > -->
        <v-data-table
          :headers="headers"
          :items="items"
          :search="search"
          :items-per-page="10"
          :hide-default-header="false"
          :loading="isLoading"
          item-key="name"
          class="sober elevation-12"
        >
          <template v-slot:body.prepend>
            <tr>
              <td>
                <v-text-field
                  label="Search"
                  type="text"
                  v-model="searchKeys.houseowner_name_np"
                ></v-text-field>
              </td>
              <td>
                <v-text-field label="Search" type="text" v-model="searchKeys.name">></v-text-field>
              </td>
              <td>
                <v-text-field label="Search" type="text" v-model="searchKeys.address"
                  >></v-text-field
                >
              </td>
              <td>
                <v-text-field label="Search" type="text" v-model="searchKeys.salutation_type_id"
                  >></v-text-field
                >
              </td>
              <td>
                <v-text-field label="Search" type="text" v-model="searchKeys.citizenship_no"
                  >></v-text-field
                >
              </td>
              <td>
                <v-text-field label="Search" type="text" v-model="searchKeys.street_no"
                  >></v-text-field
                >
              </td>
              <td>
                <v-text-field label="Search" type="text" v-model="searchKeys.house_no"
                  >></v-text-field
                >
              </td>
              <td colspan="2"></td>
            </tr>
          </template>

          <template v-slot:item.status="{ item }">
            <v-btn color="primary" small :to="{ name: 'inventory_tab', params: { id: item.id } }"
              >Add More
            </v-btn>
          </template>
        </v-data-table>
      </div>
    </v-card>
  </div>
</template>
<script>
import { VueAxios } from "@/plugins/axios";

export default {
  data() {
    return {
      // boolean to start/stop progress spinner
      isDataLoading: true,
      enabled: null,
      DESSERTS: [],
      items: [],
      search: null,
      slots: [
        "body",
        "body.append",
        "body.prepend",
        "footer",
        "header.data-table-select",
        "header",
        "progress",
        "item.data-table-select",
        "item.<name>",
        "no-data",
        "no-results",
        "top"
      ],
      headers: [
        {
          text: "घरधनीको नाम",
          align: "start",
          sortable: false,
          value: "houseowner_name"
        },
        { text: "सर्भेक्षकको नाम ", value: "surveyor_name" },
        { text: "ठेगाना (टोल)", value: "address" },
        { text: "Gender", value: "salutation_type_id" },
        { text: "घरधनीको नागरिकता न", value: "citizenship_no" },
        { text: " सडकको नाम वा नं", value: "street_no" },
        { text: "घर नं ", value: "house_no" },
        { text: "कार्य", value: "status" }
      ],
      searchKeys: {
        houseowner_name_np: "",
        name: "",
        address: "",
        salutation_type_id: "",
        citizenship_no: "",
        street_no: "",
        house_no: ""
      }
    };
  },
  computed: {
    isLoading() {
      return this.isEnabled("progress");
    }
  },

  watch: {
    enabled(slot) {
      if (slot === "no-data") {
        this.items = [];
      } else if (slot === "no-results") {
        this.search = "...";
      } else {
        this.search = null;
      }
    }
  },

  methods: {
    scrollToTop() {
      window.scrollTo(0, 0);
    },
    searchFilter() {},
    isEnabled(slot) {
      return this.enabled === slot;
    }
    // searchFilters() {
    //   // alert();
    //   // const houseowner_name_np=this.houseowner_name_np;
    //   //  debugger;
    //   const statusurl = "api/registration/search";
    //   VueAxios.post(
    //     statusurl,
    //     this.searchKeys,
    //     {
    //       headers: {
    //         "X-Requested-With": "XMLHttpRequest",
    //         Authorization: `Bearer ${localStorage.getItem("token")}`
    //       }
    //     },
    //     {
    //       // timeout: 1000000,
    //     }
    //   ).then(response => {
    //     // debugger;
    //     // this.searchKeys.houseowner_name_np = response.data.data.searchKeys;
    //     // console.log(this.DESSERTS);
    //     this.items = response.data.data;
    //   });
    // }
  },
  mounted() {
    this.scrollToTop();
    const obj = this;
    console.log(localStorage.getItem("token"));
    const url = "api/bids/infrastructure/building";
    VueAxios.get(url).then(response => {
      // debugger;
      obj.DESSERTS = response.data.data;
      obj.items = response.data.data;
      // console.log(obj.items);debugger;
      // Stop the progress bar
      obj.isDataLoading = false;
    });
  }
};
</script>
