<template>
  <div>
    <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="true"></loading>

    <v-card class="sober d-flex flex-column">
      <v-card class="sober d-flex flex-column">
        <v-toolbar color="primary" dark flat>
          <v-toolbar-title v-if="newgeneralInfo.id">
            General Information
          </v-toolbar-title>
          <v-toolbar-title v-else>
            Building Information
          </v-toolbar-title>
        </v-toolbar>

        <v-form ref="form" class="mx-4" v-model="valid">
          <v-row>
            <v-col md="4">
              <label>सर्वेक्षकको नाम <span>*</span></label>
            </v-col>
            <v-col cols="4">
              <v-text-field
                v-model="newgeneralInfo.surveyor_name"
                :rules="[required('सर्वेक्षकको नाम')]"
                label="सर्वेक्षकको नाम"
                class=" "
                clearable
                ref="dateField"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="4">
              <label> घरधनीको नाम/institution<span>*</span></label>
            </v-col>

            <v-col cols="4">
              <!-- <v-text-field v-model="newgeneralInfo.building_use_id" label=""      ></v-text-field> -->

              <v-text-field
                v-model="newgeneralInfo.houseowner_name"
                :rules="[required('घरधनीको नाम')]"
                label="घरधनीको नाम"
                dense
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="4">
              <label> लिंग<span>*</span></label>
            </v-col>

            <v-col cols="4">
              <v-select
                v-model="newgeneralInfo.salutation_type_id"
                :rules="[requiredDropdown('लिंग')]"
                label="Select लिंग"
                :items="genderType"
                item-value="id"
                item-text="gender_type"
                :key="id"
                dense
              ></v-select>
            </v-col>
          </v-row>

          <v-row>
            <v-col md="4">
              <label> घरधनीको सम्पर्क नं.<span>*</span></label>
            </v-col>
            <v-col md="4">
              <v-text-field
                v-model="newgeneralInfo.phone_no"
                :rules="[required('घरधनीको सम्पर्क नं'), maxLength('घरधनीको सम्पर्क नं', 10)]"
                label="घरधनीको सम्पर्क नं"
                dense
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col md="4">
              घरधनीको नागरिकता न
            </v-col>

            <v-col md="4">
              <v-text-field
                v-model="newgeneralInfo.citizenship_no"
                label="घरधनीको नागरिकता न"
                dense
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col md="4">
              <label> वडा नं.<span>*</span></label>
            </v-col>

            <v-col md="4">
              <v-select
                v-model="newgeneralInfo.ward_no"
                :rules="[requiredDropdown('वडा नं')]"
                label="Select ward"
                :items="wardName"
                item-value="id"
                item-text="ward_name"
                :key="id"
                dense
              ></v-select>
            </v-col>
          </v-row>
          <v-row>
            <v-col md="4">
              <label> ठेगाना (टोल)<span>*</span></label>
            </v-col>

            <v-col md="4">
              <v-text-field
                required
                v-model="newgeneralInfo.address"
                :rules="[required('ठेगाना (टोल)')]"
                label="ठेगाना (टोल)"
                dense
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col md="4">
              <label> सडकको नाम वा नं.<span>*</span></label>
            </v-col>

            <v-col cols="12" md="4">
              <v-text-field
                label="सडकको नाम वा नं."
                :rules="[required('सडकको नाम वा नं.')]"
                v-model="newgeneralInfo.street_no"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col md="4">
              <label> घर नं.<span>*</span></label>
            </v-col>
            <!-- <v-text-field v-model="newgeneralInfo.reg_id" :value="newgeneralInforole.id" type="hidden" ></v-text-field> -->
            <v-col cols="12" md="4">
              <v-text-field
                v-model="newgeneralInfo.house_no"
                :rules="[required('घर नं.'), maxLength('घर नं.', 10)]"
                label="घर नं."
                dense
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col md="4">
              <label> घरको नक्शा पास गरेको छ कि छैन <span>*</span></label>
            </v-col>
            <v-col cols="12" md="4">
              <v-radio-group dense v-model="newgeneralInfo.is_map_verified" :mandatory="true">
                <v-radio label="छ" v-bind:value="1"></v-radio>
                <v-radio label="छैन" v-bind:value="0"></v-radio>
              </v-radio-group>
            </v-col>
          </v-row>
          <v-row>
            <v-col md="4">
              नक्शा नं.
            </v-col>
            <v-col cols="12" md="4">
              <v-text-field v-model="newgeneralInfo.map_no" label="नक्शा नं." dense></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col md="4">
              नक्शा पास गरेको मिति
            </v-col>
            <v-col cols="12" md="4">
              <!-- <v-text-field
                v-model="newgeneralInfo.map_verified_date_np"
                label="नक्शा पास गरेको मिति(वि.स.)"
                class=" "
                clearable
                ref="dateField"
              ></v-text-field> -->
              <v-select
                v-model="newgeneralInfo.map_verified_date_np"
                :rules="[requiredDropdown('नक्शा पास गरेको मिति')]"
                label="Select Year"
                :items="getYear(2000, 2077)"
                dense
              ></v-select>
              <!-- <select class="form-control" v-model="newgeneralInfo.map_verified_date_np">
                <option v-for="n in getYear(2000, 2077)" :key="n" :value="n"> {{ n }} </option>
              </select> -->
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" md="4">
              <label> निर्माण सम्पन्न मिति<span>*</span></label>
            </v-col>
            <v-col cols="12" md="4">
              <!-- <v-text-field
                v-model="newgeneralInfo.construction_completion_date_np"
                :rules="[required('निर्माण सम्पन्न मिति')]"
                label="निर्माण सम्पन्न मिति"
                class=" "
                clearable
                ref="dateFieldCompletionDate"
              ></v-text-field> -->
              <v-select
                v-model="newgeneralInfo.construction_completion_date_np"
                :rules="[requiredDropdown('निर्माण सम्पन्न मिति')]"
                label="Select Year"
                :items="getYear(2000, 2077)"
                dense
              ></v-select>
              <!-- <select class="form-control" v-model="newgeneralInfo.construction_completion_date_np">
                <option v-for="n in getYear(2000, 2077)" :key="n" :value="n"> {{ n }} </option>
              </select> -->
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" md="4">
              <label> निर्माणको लागत<span>*</span></label>
            </v-col>
            <v-col cols="12" md="4">
              <!-- <v-text-field
                v-model="newgeneralInfo.construction_cost"
                :rules="[required('निर्माणको लागत'), maxLength('निर्माणको लागत', 15)]"
                label="निर्माणको लागत"
                dense
              ></v-text-field> -->
              <v-text-field
                v-model="newgeneralInfo.construction_cost"
                :rules="[required('निर्माणको लागत'), maxLength('निर्माणको लागत', 15)]"
                label="निर्माणको लागत"
                dense
              ></v-text-field>
            </v-col>
          </v-row>
          <nepaliDatePicker v-model="nepalidate" label="Nepali Date"></nepaliDatePicker>
          <v-row>
            <v-col cols="12" md="4">
              GPS Location
            </v-col>
            <v-col cols="12" md="4">
              {{ mapCordinate.latitude }}, {{ mapCordinate.longitude }}
            </v-col>
          </v-row>
          <inventory-map class="order-2" cordinateType="singleCordinate" v-model="mapCordinate" />
          <v-row>
            <v-col>
              <v-btn primary v-on:click="save" class="primary" :disabled="!valid">Submit</v-btn>

              <!-- <v-btn primary v-on:click="save" class="primary">Submit</v-btn> -->
            </v-col>
          </v-row>
        </v-form>
        <v-snackbar
          v-model="snackbar.show"
          :bottom="y === 'bottom'"
          :color="snackbar.color"
          :left="x === 'left'"
          :multi-line="mode === 'multi-line'"
          :right="x === 'right'"
          :timeout="3000"
          :top="y === 'top'"
          :vertical="mode === 'vertical'"
        >
          {{ successfulMessage }}
        </v-snackbar>
      </v-card>

      <!-- <card :url="apiUrl" /> -->
    </v-card>
  </div>
</template>

<script>
import { VueAxios } from "@/plugins/axios";
// import * as nepaliDatePicker from "../../../../../../node_modules/nepali-date-picker/dist/jquery.nepaliDatePicker.min.js"; /*eslint-disable-line*/
// import "../../../../../../node_modules/nepali-date-picker/dist/nepaliDatePicker.min.css";

export default {
  name: "newgeneralInfo",
  props: {
    nextStep: {
      type: Function
    }
  },

  data() {
    return {
      isFormLoading: false,
      isPostAuthorized: true,
      images: [],
      records: [],
      files: [],

      formData: new FormData(),

      mapCordinate: {},
      id: "",
      valid: false,

      maxLength(propertyType, maxLength) {
        return v =>
          (v && String(v).length <= maxLength) ||
          `${propertyType} must be less than ${maxLength} digits`;
      },
      required(propertyType) {
        return v => (v && String(v).length > 0) || `${propertyType} is required`;
      },
      requiredDropdown(propertyType) {
        return v => (v && Number(v) > 0) || `${propertyType} is required`;
      },
      requiredNumber(propertyType) {
        return v => (v && v > 0) || `Only number is allowed`;
      },

      user_role_id: localStorage.getItem("auth_user_role_id"),
      userRoleId: localStorage.getItem("auth_user_role_id"),
      ward_no: localStorage.getItem("ward_no"),
      is_municipal_staff: localStorage.getItem("is_municipal_staff"),
      color: { color: "#3F3C3C" },
      mode: "",
      genderType: [],
      wardName: [],
      generalInfo: [],
      newgeneralInfo: {
        id: 0,
        surveyor_name: "",
        houseowner_name: "",
        salutation_type_id: "",
        phone_no: "",
        citizenship_no: "",
        ward_no: "",
        address: "",
        street_no: "",
        house_no: "",
        is_map_verified: "",
        map_verified_date: "",
        map_verified_date_np: "",
        map_no: "",
        construction_completion_date: "2012-3-3",
        construction_completion_date_np: "",
        construction_cost: "",
        latitude: 0,
        longitude: 0
      },
      apiUrl: `api/registration/1/charkilla`,
      newgeneralInforole: [],
      // newgeneralInfoList: [],
      successfulMessage: "",
      snackbar: {
        show: false,
        message: this.sucessfulMessage,
        color: "success"
      }
    };
  },

  computed: {
    years() {
      const year = new Date().getFullYear();
      return Array.from({ length: year - 1900 }, (value, index) => 1901 + index);
    },
    isLoading() {
      if (this.genderType.length === 0 || this.wardName.length === 0) {
        // all drop-down values not initiated from api
        return true;
      } else if (this.isFormLoading) {
        return true;
      }
      return false;
    }
  },
  created() {
    this.selectBoxApi();
    this.buildingInfoDetail();
  },

  mounted() {
    const obj = this;
    const dateField = this.$refs.dateField;
    if (dateField) {
      $(dateField.$el).nepaliDatePicker({
        dateFormat: "%y-%m-%d",
        closeOnDateSelect: true
      });
      $(dateField.$el).on("dateSelect", event => {
        // debugger;
        console.log(`Date: ${event.datePickerData.formattedDate}`);
        obj.newgeneralInfo.map_verified_date_np = event.datePickerData.formattedDate;
      });
    }
    const dateFieldCompletionDate = this.$refs.dateFieldCompletionDate;
    if (dateFieldCompletionDate) {
      $(dateFieldCompletionDate.$el).nepaliDatePicker({
        dateFormat: "%y-%m-%d",
        closeOnDateSelect: true
      });
      $(dateFieldCompletionDate.$el).on("dateSelect", event => {
        console.log(`Date: ${event.datePickerData.formattedDate}`);
        obj.newgeneralInfo.construction_completion_date_np = event.datePickerData.formattedDate;
      });
    }
  },
  methods: {
    buildingInfoDetail() {
      const obj = this;
      // debugger;
      const url = `/api/bids/infrastructure/building/${this.$route.params.regId}`;
      VueAxios.get(url)
        .then(response => {
          obj.newgeneralInfo = response.data.data;
          obj.newgeneralInfo.construction_completion_date_np = parseInt(
            obj.newgeneralInfo.construction_completion_date_np
          );
          obj.newgeneralInfo.map_verified_date_np = parseInt(
            obj.newgeneralInfo.map_verified_date_np
          );
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    getYear(start, stop) {
      return new Array(stop - start).fill(start).map((n, i) => n + i);
    },
    selectBoxApi() {
      const obj = this;
      const genderUrl = "api/bids/select-box/gender";
      VueAxios.get(genderUrl)
        .then(response => {
          obj.genderType = response.data;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
      const wardUrl = "api/bids/select-box/ward";
      VueAxios.get(wardUrl)
        .then(response => {
          const wardList = response.data;
          //  debugger;
          obj.wardName = wardList;
          if (obj.is_municipal_staff == "0") {
            obj.wardName = wardList.filter(w => w.id == obj.ward_no);
            this.selectWard(wardList, obj.ward_no, obj.newgeneralInfo.ward_no);
          }
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    selectWard(wardList, selectedValue, element) {
      element = wardList.filter(w => w.id == selectedValue)[0];
    },
    save() {
      this.isFormLoading = true;
      //   debugger;
      if (
        this.newgeneralInfo.id === "null" ||
        this.newgeneralInfo.id === 0 ||
        this.newgeneralInfo.id === undefined
      ) {
        this.newgeneralInfo.latitude = this.mapCordinate.latitude;
        this.newgeneralInfo.longitude = this.mapCordinate.longitude;
        const saveUrl = `/api/bids/infrastructure/building`;
        VueAxios.post(saveUrl, this.newgeneralInfo).then(response => {
          this.isFormLoading = false;
          if (response.error != null) {
            this.message = response.error;
          } else {
            console.log(localStorage.getItem("token"));
            this.$router.push({ name: "inventorydashboard" });
          }
        });
      } else {
        this.isFormLoading = true;
        const eurl = `/api/bids/infrastructure/building/${this.$route.params.regId}`;
        VueAxios.put(eurl, this.newgeneralInfo)
          .then(response => {
            this.isFormLoading = false;
            if (response.status === 200 || response.status === 201) {
              this.snackbar.show = true;
              this.successfulMessage = "Data Updated successfully";
            }

            console.log(response);
          })
          .catch(error => {
            this.snackbar.show = true;
            this.successfulMessage = "Save failed.";
            this.$refs.form.reset();
            console.log(error);
          });
      }
    },

    edit(id) {
      // debugger;
      //    const url = `api/registration/${this.$route.params.regId}/application/${this.$route.params.id}`;
      const url = `api/bids/infrastructure/building/${id}`;
      VueAxios.get(url).then(response => {
        // debugger;
        this.newgeneralInfo = response.data.data;
        console.log(response.data.data);
      });
    }
  }
};
</script>
<style scoped>
label span {
  color: red;
}
.card {
  padding: 15px;
}
</style>
