<style>
.houseownerinfo {
  font-weight: normal;
  width: 250px;
}
</style>
<template>
  <v-layout class="mb-9">
    <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="true"></loading>

    <v-flex>
      <v-card class="d-flex flex-column mb-2">
        <div>
          <v-btn class="primary" :to="{ name: 'inventorydashboard' }">Back</v-btn>
        </div>
        <v-row>
          <v-col sm="12">
            <v-toolbar dense dark flat color="primary">
              <v-toolbar-title>General Information</v-toolbar-title>
            </v-toolbar>
            <v-simple-table class="sober elevation-2">
              <template v-slot:default>
                <!-- <thead>
                                                <tr class="registrationtables">
                                                    <th npm run serve
                                                    class="text-left registrationtables"></th>
                                                </tr>
                                            </thead> -->
                <tbody>
                  <tr class="">
                    <td class="registrationtables">
                      सर्वेक्षकको नाम: <b> {{ results.surveyor_name }} </b>
                    </td>
                    <td class="registrationtables">
                      घरधनीको नाम: <b>{{ results.houseowner_name }}</b>
                    </td>
                    <td class="registrationtables">
                      लिंग: <b>{{ results.salutation_type_id }}</b>
                    </td>
                    <td class="registrationtables">
                      घरधनीको सम्पर्क नं: <b>{{ results.phone_no }}</b>
                    </td>
                    <td class="registrationtables">
                      सडकको नाम वा नं <b>{{ results.street_no }}</b>
                    </td>
                  </tr>
                  <tr class="registrationtables">
                    <td class="registrationtables">
                      घरधनीको नागरिकता न: <b>8{{ results.citizenship_no }}</b>
                    </td>
                    <td class="registrationtables">
                      वडा नं: <b>8{{ results.ward_no }}</b>
                    </td>
                    <td class="registrationtables">
                      ठेगाना (टोल): <b>{{ results.address }}</b>
                    </td>
                    <td class="registrationtables">
                      वडा नं: <b>{{ results.ward_no }}</b>
                    </td>
                    <td class="registrationtables">
                      घर नं.: <b>{{ results.house_no }}</b>
                    </td>
                  </tr>
                  <tr class="registrationtables">
                    <td class="registrationtables">
                      नक्शा नं.: <b>8{{ results.map_no }}</b>
                    </td>
                    <td class="registrationtables">
                      नक्शा पास गरेको मिति: <b>8{{ results.map_verified_date_np }}</b>
                    </td>
                    <td class="registrationtables">
                      निर्माण सम्पन्न मिति: <b>{{ results.construction_completion_date_np }}</b>
                    </td>
                    <td class="registrationtables">
                      निर्माणको लागत: <b>{{ results.construction_cost }}</b>
                    </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
            <v-simple-table class="registrationtables">
              <template v-slot:default> </template>
            </v-simple-table>
          </v-col>
          <!-- <v-col sm="4">
            <v-card class="sober elevation-2">
              <v-toolbar dense dark flat color="primary">
                <v-toolbar-title>Application Status</v-toolbar-title>
              </v-toolbar>
              <v-card-text> -->
          <!-- <strong>{{ log }}</strong> -->
          <!-- <br /> -->
          <!-- <strong> Phase: </strong> {{ results.phase }} -->
          <!-- </v-card-text> -->
          <!-- <h4>View Application status logs</h4> -->
          <!-- </v-card>
          </v-col> -->
        </v-row>
      </v-card>
      <v-card>
        <v-stepper v-model="el">
          <!-- <v-stepper-header class="py-0 my-0">
            <template v-for="n in steps">
              <v-stepper-step
                :key="n.sn"
                :step="n.sn"
                :complete="n.sn <= results.phase_id || n.sn == '6'"
                :editable="n.sn <= results.phase_id || n.sn == '6'"
              >
                {{ n.text }}
              </v-stepper-step>
            </template>
          </v-stepper-header> -->
          <v-stepper-items>
            <v-stepper-content
              v-for="n in steps"
              :key="`${n.sn}-content`"
              :step="n.sn"
              class="sober px-1 "
            >
              <component
                :is="getComponent(n.sn)"
                :id="results.id"
                v-if="n.sn === el"
                :next-step="nextStep"
              />
            </v-stepper-content>
          </v-stepper-items>
        </v-stepper>
      </v-card>
    </v-flex>
  </v-layout>
</template>
<script>
import { VueAxios } from "@/plugins/axios";
import bidLevel from "./components/bidLevel.vue";

const COMPONENT_MAP = {
  1: "bidLevel"
  // 2: "map"
};
export default {
  name: "InventoryTab",
  data() {
    return {
      el: 1,
      steps: [
        {
          text: "Inventory System",
          sn: 1
        }
      ],
      results: [],
      log: ""
    };
  },
  components: {
    bidLevel
    // Map
    // Test
  },
  computed: {
    isLoading() {
      if (this.results === undefined || this.results.length === 0) {
        // registration value not yet initiated from api. Loading spinner active
        return true;
      }
      return false;
    }
  },
  watch: {
    steps(val) {
      if (this.el > val) {
        this.el = val;
      }
    }
  },
  mounted() {
    // debugger;
    const url = `api/bids/infrastructure/building/${this.$route.params.id}`;
    VueAxios.get(url).then(response => {
      // debugger;
      this.results = response.data.data;
      // sconsole.log(response.data.data);

      // Select the tab that the registration phase is in
      // this.el = this.results.phase_id;
    });
  },
  methods: {
    getComponent(n) {
      return COMPONENT_MAP[n];
    },
    nextStep(n) {
      // if (n === this.results.phase_id) {
      //   this.el = n;
      // } else {
      //   this.el = this.results.phase_id;
      // }
      if (n === this.steps.sn) {
        this.el = 1;
      } else {
        this.el = n + 1;
      }
    }
  }
};
</script>
