<template>
  <v-layout class="my-3">
    <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="true"></loading>
    <v-container class="pa-0" dense>
      <v-row class="" dense>
        <v-col xs12 md12 lg12>
          <v-card class="pa-0">
            <v-btn class="primary my-2" :to="{ name: 'infrastructuredashboard' }">Back</v-btn>

            <v-toolbar color="primary " dark>
              <v-col class="d-flex justify-space-around">
                <v-toolbar-title class="text-center justify-center">
                  Water Supply Infrastructures Inventory System
                </v-toolbar-title>
              </v-col>
            </v-toolbar>
          </v-card>
        </v-col>
      </v-row>
      <v-row class="elevation-12" dense>
        <v-col xs12 md8 lg8>
          <v-card class="nibedanBibaran elevation-12">
            <v-toolbar color="primary" dark>
              <v-toolbar-title>
                Information
              </v-toolbar-title>
            </v-toolbar>
            <v-form ref="form" class="mx-4" v-model="valid">
              <v-row>
                <v-col cols="4">
                  <label>Name<span>*</span></label>
                </v-col>
                <v-col cols="8">
                  <v-text-field
                    v-model="newwaterSupplyDetails.name"
                    :rules="[required('Name'), maxLength('Name', 30)]"
                    label="Name"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col md="4">
                  <label> Ward Number<span>*</span></label>
                </v-col>

                <v-col md="8">
                  <v-select
                    v-model="newwaterSupplyDetails.ward_no"
                    :rules="[requiredDropdown('Ward Number')]"
                    label="Select ward"
                    :items="wardName"
                    item-value="id"
                    item-text="ward_name"
                    :key="id"
                    dense
                  ></v-select>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="4">
                  <label> Type of water supply structure<span>*</span></label>
                </v-col>
                <v-col cols="8">
                  <v-select
                    v-model="newwaterSupplyDetails.water_supply_structure_type_id"
                    :rules="[requiredDropdown('Type of water supply structure')]"
                    :items="waterSupplyStructureType"
                    item-value="id"
                    item-text="water_supply_structure_type"
                    :key="id"
                    dense
                  ></v-select>
                </v-col>
              </v-row>

              <v-row>
                <v-col cols="4">
                  <label>Capacity (ltrs.)<span>*</span></label>
                </v-col>
                <v-col cols="8">
                  <v-text-field
                    v-model="newwaterSupplyDetails.capacity_ltrs"
                    :rules="[required('Capacity'), maxLength('Capacity', 10)]"
                    label="Capacity (ltrs.)"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col md="4">
                  <label> Coordinates<span>*</span></label>
                </v-col>
                <v-col cols="8"> {{ mapCordinate.latitude }}, {{ mapCordinate.longitude }} </v-col>
              </v-row>
              <v-snackbar
                v-model="snackbar.show"
                :bottom="y === 'bottom'"
                :color="snackbar.color"
                :left="x === 'left'"
                :multi-line="mode === 'multi-line'"
                :right="x === 'right'"
                :timeout="3000"
                :top="y === 'top'"
                :vertical="mode === 'vertical'"
              >
                {{ successfulMessage }}
              </v-snackbar>
              <v-btn
                primary
                v-on:click="save"
                v-if="isPostAuthorized"
                class="primary"
                :disabled="!valid"
                >Submit</v-btn
              >
              <br />
            </v-form>
             <card   :url="apiUrl" :column-header="headerList" :href="href" :action-list="actionList" :photoL-list="photoView" v-on:editdetail="edit"/>
          </v-card>
        </v-col>
        <v-col xs12 md4 lg4>
          <inventory-map class="order-2" cordinateType="singleCordinate" v-model="mapCordinate" />
          <photographs-upload @file-upload="newwaterSupplyDetails.photo = $event" />
            <gallery :photo-list="PhotoList"/>
        </v-col>
      </v-row>
    </v-container>
  </v-layout>
</template>
<script>
import { VueAxios } from "@/plugins/axios";

export default {
  name: "Login",
  data() {
    return {
      isPostAuthorized: true,

      isFormLoading: false,
      headerList: ["name", "ward_no", "capacity_ltrs", "id"],
      href: ["id"],
      actionList: ["Edit"],
      photoView: ["photo"],
  PhotoList:[],
      apiUrl: `api/bids/infrastructure/water-supply-structure`,
      valid: false,
      maxLength(propertyType, maxLength) {
        return v =>
          (v && String(v).length <= maxLength) || `${propertyType} must be less than ${maxLength} digits`;
      },
      required(propertyType) {
        return v => (v && String(v).length > 0) || `${propertyType} is required`;
      },
      requiredDropdown(propertyType) {
        return v => v || `${propertyType} is required`;
      },
      requiredNumber(propertyType) {
        return v => (v && v > 0) || `Only number is allowed`;
      },
      mapCordinate: {},
      successfulMessage: "",
      wardName: [],
      waterSupplyStructureType: [],
      snackbar: {
        show: false,
        message: this.sucessfulMessage,
        color: "success"
      },

      waterSupplyDetails: [],
      newwaterSupplyDetails: {
        name: "",
        water_supply_structure_type_id: "",
        latitude: "",
        longitude: "",
        capacity_ltrs: "",
        ward_no: "",
        photo: []
      },

      type_id: "",
      user_role_id: localStorage.getItem("auth_user_role_id"),
      id: "single",
      color: { color: "#3F3C3C" }
    };
  },
  created() {
    this.selectBoxApi();
  },
  computed: {
    isLoading() {
      if (this.waterSupplyStructureType.length === 0 || this.wardName.length === 0) {
        // all drop-down values not initiated from api
        return true;
      } else if (this.isFormLoading) {
        return true;
      }
      return false;
    }
  },
  mounted() {
    const obj = this;
    const url = `api/registration/1/charkilla`;
    VueAxios.get(url)
      .then(response => {
        // debugger;
        obj.records = response.data.data;
        console.log(obj.records);
        this.$root.$emit("valueList", obj.records);
      })
      .catch(error => {
        console.log(`api error:${error}`);
      });
    const wardUrl = "api/bids/select-box/ward";
    VueAxios.get(wardUrl)
      .then(response => {
        obj.wardName = response.data;
        // console.log(obj.buildingPermanent);
        // debugger;
      })
      .catch(error => {
        console.log(`api error:${error}`);
      });
  },
  methods: {
    selectBoxApi() {
      const obj = this;
      const waterSupplyStructureTypeUrl = "api/bids/select-box/water-supply-structure";
      VueAxios.get(waterSupplyStructureTypeUrl)
        .then(response => {
          obj.waterSupplyStructureType = response.data;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    save() {
      this.isFormLoading = true;
      this.newwaterSupplyDetails.latitude = this.mapCordinate.latitude;
      // debugger;
      this.newwaterSupplyDetails.longitude = this.mapCordinate.longitude;
      const formData = new FormData();
      const files = this.newwaterSupplyDetails.photo;

      if (files.length > 0 && files[0].size < 1024 * 1024 * 5) {
        for (let i = 0; i < files.length; i += 1) {
          formData.append("photo[]", files[i], files[i].name);
        }
      }
      formData.append(
        "water_supply_structure_type_id",
        this.newwaterSupplyDetails.water_supply_structure_type_id
      );
      formData.append("name", this.newwaterSupplyDetails.name);
      formData.append("ward_no", this.newwaterSupplyDetails.ward_no);
      formData.append("latitude", this.newwaterSupplyDetails.latitude);
      formData.append("longitude", this.newwaterSupplyDetails.longitude);
      formData.append("capacity_ltrs", this.newwaterSupplyDetails.capacity_ltrs);

      console.log(formData);
      formData.append("_method", "POST");

      const saveUrl = `api/bids/infrastructure/water-supply-structure`;
      VueAxios.post(saveUrl, formData, {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      })
        .then(response => {
          //  alert(1);
          this.isFormLoading = false;

          if (response.status === 200 || response.status === 201) {
            this.waterSupplyDetails.push(response.data.data);
            // this.$toastr.s("All images uplaoded successfully");
            this.newwaterSupplyDetails.photo = [];
            // this.photo = [];
            this.snackbar.show = true;
            this.successfulMessage = "Record added successfully.";
            this.$refs.form.reset();
          }
        })
        .catch(error => {
          // alert(2);
          this.isFormLoading = false;
          this.snackbar.show = true;
          this.successfulMessage = "Saved Failed.";
          console.log(`api error:${error}`);
        });
    },
     edit(id) {
      // debugger;
      const obj = this;
      //    const url = `api/registration/${this.$route.params.regId}/application/${this.$route.params.id}`;
      const url = `api/bids/infrastructure/water-supply-structure/${id}`;
      VueAxios.get(url).then(response => {
        // debugger;
        obj.newwaterSupplyDetails = response.data.data;
        obj.PhotoList=obj.newwaterSupplyDetails.photos;
      
      });
    }
  }
};
</script>
<style scoped>
label span {
  color: red;
}
.card {
  padding: 15px;
}
</style>
