        style(feature) {
          switch (feature.properties.party) {
            case "Republican":
              return { color: "#04316a" };
            case "Democrat":
              return { color: "#006fb9" };
            case "Dolakha2":
              return { color: "#000" };
            case "Dolakha-ward":
              return { color: "#3F3C3C" };
            default:
              return { color: "#228b22" };
          }
        }
      }
    };
  },
  created() {
    this.getFiscalYear();
    this.constructionType();
  },
  mounted() {
    const { dateField } = this.$refs;
    if (dateField) {
      $(dateField.$el).nepaliDatePicker({
        dateFormat: "%y-%m-%d",
        closeOnDateSelect: true
      });
      $(dateField.$el).on("dateSelect", event => {
        this.registration.registration_date_np = event.datePickerData.formattedDate;
      });
    }
  },
  methods: {
    getDate() {},
    getFiscalYear() {
      const fiscalUrl = "api/fiscal-year";
      VueAxios.get(fiscalUrl, {
        headers: {
          "X-Requested-With": "XMLHttpRequest",
          Authorization: `Bearer ${localStorage.getItem("token")}`
        }
      })
        .then(response => {
          this.fiscalyear = response.data.data;
        })
        .catch(() => {});
    },
    constructionType() {
      const constructionurl = "api/construction-type";
      VueAxios.get(constructionurl, {
        headers: {
          "X-Requested-With": "XMLHttpRequest",
          Authorization: `Bearer ${localStorage.getItem("token")}`
        }
      })
        .then(response => {
          this.constructiontype = response.data;
        })
        .catch(() => {});
    },
    add(e) {
      // console.log(e.latlng);
      this.marker = L.latLng(e.latlng.lat, e.latlng.lng);
      this.registration.latitude = e.latlng.lat;
      this.registration.longitude = e.latlng.lng;
    },
    changeMarker() {
      this.marker = L.latLng(this.registration.latitude, this.registration.longitude);
    },
    save() {
      // start the loading spinner
      this.isLoading = true;
      console.log(this.registration);
      const url = "api/registration";
      VueAxios.post(
        url,
        this.registration,
        {
          headers: {
            "X-Requested-With": "XMLHttpRequest",
            Authorization: `Bearer ${localStorage.getItem("token")}`
          }
        },
        { timeout: 10000 }
      ).then(response => {
        this.isLoading = false;
        if (response.error != null) {
          this.message = response.error;
        } else {
          // this.token = response.data.access_token;
          // console.log(response.data.access_token);
          // localStorage.setItem('token', response.data.access_token);
          console.log(localStorage.getItem("token"));
          this.$router.push({ name: "home" });
        }
      });
    }
  }
};
