          <v-simple-table class="sober">
            <template v-slot:default>
              <thead>
                <tr class="registrationtable">
                  <th class="text-left registrationtable" v-for="p in revenueHeader" :key="p">
                    {{ p }}
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr
