    getDate() {
      const { dateField } = this.$refs;
      if (dateField) {
        $(dateField.$el).nepaliDatePicker({
          dateFormat: "%y-%m-%d",
          closeOnDateSelect: true
        });
        $(dateField.$el).on("dateSelect", event => {
          this.newconsultancy.registration_date_np = event.datePickerData.formattedDate;
