<template>
  <v-layout class="my-3">
    <loading :active.sync="isLoading" :can-cancel="true" :is-full-page="true"></loading>
    <v-container class="pa-0" dense>
      <v-row class="" dense>
        <v-col xs12 md12 lg12>
          <v-card class="pa-0">
            <v-btn class="primary my-2" :to="{ name: 'infrastructuredashboard' }">Back</v-btn>

            <v-toolbar color="primary " dark>
              <v-col class="d-flex justify-space-around">
                <v-toolbar-title class="text-center justify-center">
                  Other Infrastructures Inventory System
                </v-toolbar-title>
              </v-col>
            </v-toolbar>
          </v-card>
        </v-col>
      </v-row>
      <v-row class="elevation-12" dense>
        <v-col xs12 md8 lg8>
          <v-card class="nibedanBibaran elevation-12">
            <v-toolbar color="primary" dark>
              <v-toolbar-title>
                Information
              </v-toolbar-title>
            </v-toolbar>
            <v-form ref="form" class="mx-4" v-model="valid">
              <v-row>
                <v-col md="4">
                  <label> Name.<span>*</span></label>
                </v-col>

                <v-col md="8">
                  <v-text-field
                    label="Name"
                    v-model="newotherInfo.name"
                    :rules="[required('name'), maxLength('name', 30)]"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col md="4">
                  <label> वडा नं.<span>*</span></label>
                </v-col>

                <v-col md="8">
                  <v-select
                    v-model="newotherInfo.ward_no"
                    :rules="[requiredDropdown('वडा नं')]"
                    label="Select ward"
                    :items="wardName"
                    item-value="id"
                    item-text="ward_name"
                    :key="id"
                    dense
                  ></v-select>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="4">
                  <label> Type of infrastructure<span>*</span></label>
                </v-col>
                <v-col cols="8">
                  <v-select
                    v-model="newotherInfo.other_infrastructure_type_id"
                    :rules="[requiredDropdown('Infrastructure Type')]"
                    :items="otherInfrastructureType"
                    item-value="id"
                    item-text="other_infrastructure_type"
                    :key="id"
                    dense
                  ></v-select>
                </v-col>
              </v-row>
              <!-- <v-row>
                <v-col cols="4">
                  <label> Length Unit Type<span>*</span></label>
                </v-col>
                <v-col cols="8">
                  <v-select
                    v-model="newotherInfo.length_unit_type_id"
                    :rules="[requiredDropdown('Length Type Unit')]"
                    :items="unitType"
                    item-value="id"
                    item-text="unit_type_en"
                    :key="id"
                    dense
                  ></v-select>
                </v-col>
              </v-row> -->
              <v-row>
                <v-col cols="4">
                  <label> Length(m)<span>*</span></label>
                </v-col>
                <v-col cols="8">
                  <v-text-field
                    label="length"
                    v-model="newotherInfo.length"
                    :rules="[requiredNumber('Length'), maxLength('Length', 10)]"
                  ></v-text-field>
                </v-col>
              </v-row>
              <!-- <v-row>
                <v-col cols="4">
                  <label> Width Unit Type<span>*</span></label>
                </v-col>
                <v-col cols="8">
                  <v-select
                    v-model="newotherInfo.width_unit_type_id"
                    :rules="[requiredDropdown('Length Type Unit')]"
                    :items="unitType"
                    item-value="id"
                    item-text="unit_type_en"
                    :key="id"
                    dense
                  ></v-select>
                </v-col>
              </v-row> -->
              <v-row>
                <v-col cols="4">
                  <label> Width(m)<span>*</span></label>
                </v-col>
                <v-col cols="8">
                  <v-text-field
                    label="width"
                    v-model="newotherInfo.width"
                    :rules="[requiredNumber('Width'), maxLength('Width', 10)]"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="4">
                  <label> Material Used<span>*</span></label>
                </v-col>
                <v-col cols="8">
                  <v-text-field
                    label="Material Used"
                    v-model="newotherInfo.material_used"
                    :rules="[required('Material Used')]"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col md="4">
                  <label> Coordinates<span>*</span></label>
                </v-col>
              </v-row>

              <v-row>
                <v-col cols="8"> {{ mapCordinate.latitude }}, {{ mapCordinate.longitude }} </v-col>
              </v-row>

              <v-snackbar
                v-model="snackbar.show"
                :bottom="y === 'bottom'"
                :color="snackbar.color"
                :left="x === 'left'"
                :multi-line="mode === 'multi-line'"
                :right="x === 'right'"
                :timeout="3000"
                :top="y === 'top'"
                :vertical="mode === 'vertical'"
              >
                {{ successfulMessage }}
              </v-snackbar>
              <v-btn
                primary
                v-on:click="save"
                v-if="isPostAuthorized"
                class="primary"
                :disabled="!valid"
                >Submit</v-btn
              >
              <br />
            </v-form>
          <card   :url="apiUrl" :column-header="headerList" :href="href" :action-list="actionList" :photoL-list="photoView" v-on:editdetail="edit"/>
          </v-card>
        </v-col>
        <v-col xs12 md4 lg4>
          <inventory-map class="order-2" cordinateType="singleCordinate" v-model="mapCordinate" />
          <photographs-upload @file-upload="newotherInfo.photo = $event" />
           <gallery :photo-list="PhotoList"/>
        </v-col>
      </v-row>
    </v-container>
  </v-layout>
</template>
<script>
import { VueAxios } from "@/plugins/axios";

export default {
  name: "Login",
  data() {
    return {
      isPostAuthorized: true,
      isFormLoading: false,
      headerList: [ "name", "ward_no", "length", "width", "id"],
      href: ["id"],
      actionList: ["Edit"],
      photoView: ["photo"],
      apiUrl: `api/bids/infrastructure/other-infrastructure`,
        PhotoList:[],
      otherInfrastructureType: [],
      images: [],

      unitType: [],
      wardName: [],
      records: [],
      mapCordinate: {},
      files: [],
      formData: new FormData(),
      otherInfo: [],
      newotherInfo: {
        id: 0,
        other_infrastructure_type_id: "",
        latitude: "",
        longitude: "",
        length_unit_type_id: 1,
        length: "",
        width_unit_type_id: 1,
        width: "",
        material_used: "",
        photo: []
      },
      valid: false,
      maxLength(propertyType, maxLength) {
        return v =>
          (v && v.length <= maxLength) || `${propertyType} must be less than ${maxLength} digits`;
      },
      required(propertyType) {
        return v => (v && String(v).length > 0) || `${propertyType} is required`;
      },
      requiredDropdown(propertyType) {
        return v => v || `${propertyType} is required`;
      },
      requiredNumber(propertyType) {
        return v => (v && v > 0) || `Only number is allowed`;
      },

      successfulMessage: "",
      snackbar: {
        show: false,
        message: this.sucessfulMessage,
        color: "success"
      },
      latitude: 0,
      longitude: 0,
      roadCordinate: [],
      type_id: "",
      user_role_id: localStorage.getItem("auth_user_role_id"),
      id: "single",

      color: { color: "#3F3C3C" }
    };
  },

  computed: {
    isLoading() {
      if (
        this.otherInfrastructureType.length === 0 ||
        this.wardName.length === 0 ||
        this.unitType.length === 0
      ) {
        // all drop-down values not initiated from api
        return true;
      } else if (this.isFormLoading) {
        return true;
      }
      return false;
    }
  },
  created() {
    this.selectBoxApi();
  },

  methods: {
    selectBoxApi() {
      const obj = this;
      const otherInfrastructureTypeUrl = "api/bids/select-box/other-infrastructure";
      VueAxios.get(otherInfrastructureTypeUrl)
        .then(response => {
          obj.otherInfrastructureType = response.data;
          // console.log(obj.otherInfrastructureType);
          // debugger;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
      const wardUrl = "api/bids/select-box/ward";
      VueAxios.get(wardUrl)
        .then(response => {
          obj.wardName = response.data;
          // console.log(obj.buildingPermanent);
          // debugger;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
      const unitTypeUrl = "api/bids/select-box/unit-type";
      VueAxios.get(unitTypeUrl)
        .then(response => {
          obj.unitType = response.data;
          // console.log(obj.buildingPermanent);
          // debugger;
        })
        .catch(error => {
          console.log(`api error:${error}`);
        });
    },
    save() {
      this.isFormLoading = true;
      this.newotherInfo.latitude = this.mapCordinate.latitude;
      this.newotherInfo.longitude = this.mapCordinate.longitude;
      const formData = new FormData();
      const files = this.newotherInfo.photo;

      if (files.length > 0 && files[0].size < 1024 * 1024 * 5) {
        for (let i = 0; i < files.length; i += 1) {
          formData.append("photo[]", files[i], files[i].name);
        }
      }
      formData.append("ward_no", this.newotherInfo.ward_no);
      formData.append("name", this.newotherInfo.name);
      formData.append(
        "other_infrastructure_type_id",
        this.newotherInfo.other_infrastructure_type_id
      );
      formData.append("length_unit_type_id", this.newotherInfo.length_unit_type_id);
      formData.append("length", this.newotherInfo.length);

      formData.append("latitude", this.newotherInfo.latitude);
      formData.append("longitude", this.newotherInfo.longitude);
      formData.append("width_unit_type_id", this.newotherInfo.width_unit_type_id);
      formData.append("width", this.newotherInfo.width);
      formData.append("material_used", this.newotherInfo.material_used);

      console.log(formData);
      formData.append("_method", "POST");

      const saveUrl = `api/bids/infrastructure/other-infrastructure`;
      VueAxios.post(saveUrl, formData, {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      })
        .then(response => {
          // alert(12);
          this.isFormLoading = false;

          if (response.status === 200 || response.status === 201) {
            this.otherInfo.push(response.data.data);
            // this.$toastr.s("All images uplaoded successfully");
            this.newotherInfo.photo = [];
            // this.photo = [];
            this.snackbar.show = true;
            this.successfulMessage = "Record added successfully.";
            this.$refs.form.reset();
          }
        })
        .catch(error => {
          // alert(1);
          this.isFormLoading = false;
          this.snackbar.show = true;
          this.successfulMessage = "Saved Failed.";
          console.log(`api error:${error}`);
        });
    },
     edit(id) {
      // debugger;
      const obj = this;
      //    const url = `api/registration/${this.$route.params.regId}/application/${this.$route.params.id}`;
      const url = `api/bids/infrastructure/other-infrastructure/${id}`;
      VueAxios.get(url).then(response => {
        // debugger;
        obj.newotherInfo = response.data.data;
        obj.PhotoList=obj.newotherInfo.photos;
      
      });
    }
  }
};
</script>
<style scoped>
label span {
  color: red;
}
.card {
  padding: 15px;
}
</style>
